import BaseService from './baseService';
import endpoints from '../constants/endpoints';
import http from '../axios';
import qs from 'query-string';
import _omit from 'lodash/omit';

class RFWriterClonesService extends BaseService {
  constructor() {
    super(endpoints.rfWriterClones);
  }

  fetchAll() {
    let endpoint = endpoints.rfWriterClones();
    return http.get(endpoint);
  }

  fetch = (query = {}) => {
    const producerId = query.uuid || query.id;
    if (!producerId) {
      throw new Error('Producer Id is required for fetching writers clones of producers');
    }

    let endpoint = endpoints.rfProducers(producerId);
    query = _omit(query, ['uuid', 'id']);

    if (query && Object.keys(query).length > 0) {
      endpoint = endpoint + '?' + qs.stringify(query);
    }

    return http.get(endpoint).then(data => {
      return { dataItems: data.writer_clones, extraData: _omit(data, ['writer_clones']) };
    });
  };
}

export const rfWriterClonesService = new RFWriterClonesService();
