<template>
  <div>
    <generic-grid
      :gridName="gridName"
      :isHeaderConfigurable="true"
      :params="params"
      :service="service"
      :headers="gridHeaders"
      :toolbarTitle="toolbarTitle"
      :getDataCb="getDataCb"
      :creatable="false"
      :deletable="true"
    >
    </generic-grid>
  </div>
</template>
<script>
import GenericGrid from '../../Common/GenericGrid.vue';
import * as gridNames from '../../../constants/gridNames';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import { rfWritersService } from '../../../services/rfWritersService';
import { rfWriterClonesService } from '../../../services/rfWriterClonesService';
import * as rfWriterActionsService from '../../../services/rfWriterActionsService';
const dialogNames = {};

export default {
  name: 'rf-writers-grid',
  components: { GenericGrid, TooltipIconButton },
  props: ['params', 'getDataCb'],
  data() {
    return {
      selectedItem: {},
      dialogNames: { ...dialogNames },
      dialog: {},
      gridName: gridNames.RF_WRITER_CLONES,
      service: rfWriterClonesService,
      toolbarTitle: 'Writers Clones',
      gridHeaders: [
        {
          text: 'Producer uuid',
          align: 'center',
          value: 'producer_uuid',
          sortable: true,
        },
        {
          text: 'Source uuid',
          align: 'center',
          value: 'source_uuid',
          sortable: true,
        },
      ],
    };
  },
  computed: {
    producer() {
      return {
        uuid: this.params.uuid,
      };
    },
  },
  methods: {
    toggleDialog(dialogName, item, syncGrid) {
      this.selectedItem = item;
      this.syncGrid = syncGrid;
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
    onAssignWriteAction(body) {
      const uuid = this.selectedItem.uuid;
      return rfWriterActionsService.postAction(uuid, body).then(data => {
        if (typeof this.syncGrid === 'function') this.syncGrid();
        return data;
      });
    },
    constructWriterClones(formValues) {
      return {
        consumer_side_flow_uuid: formValues.consumer_side_flow_uuid,
        consumer_side_stream_uuid: formValues.consumer_side_stream_uuid,
        remote_producer_uuid: this.producer.uuid,
      };
    },
    onCreateWriterClones(formValues) {
      return rfWritersService.createWriter(this.constructWriterClones(formValues));
    },
  },
};
</script>
